import { Capacitor } from '@capacitor/core'
import { Filesystem } from '@capacitor/filesystem'
import { FirebaseAuthentication } from '@ionic-native/firebase-authentication'
import { IonContent, IonSpinner } from '@ionic/react'
import StarIcon from '@material-ui/icons/Star'
import firebase from 'firebase/compat/app'
import "firebase/compat/auth"
import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef, useState } from 'react'
import { RouteComponentProps } from 'react-router'
import {
  AdminItemDetail,
  AdminItemFieldRegistro, FooterFormulario,
  ModalVerificarPinFirebase,
  PageApp,
  PhotoLoaderModalRegistroMultiple,
  SelectorModalRegistro
} from '../../components'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP } from '../../constants'
import {
  comprimirImagen,
  format_numero_telefono_input,
  format_numero_telefono_verificar,
  useApiToaster,
  useOpenPage,
  useToaster
} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import '../Cliente/ClienteDetail.css'
import './PerfilTransportista.css'

const moment = require('moment-timezone')

type DatosType = {
  id: number | null
  nombre: string | null
  apellido: string | null
  nro_documento: string | null
  email: string | null
  nro_telefono: string | null
  razon_social: string | null
  ruc: string | null
  ciudades: string | null
  imagen_perfil: string | null
  fecha_nacimineto: string | null
}

type ErrorsType = {
  id: string | null
  nombre: string | null
  apellido: string | null
  nro_documento: string | null
  email: string | null
  nro_telefono: string | null
  razon_social: string | null
  ruc: string | null
  ciudades: string | null
  imagen_perfil: string | null
  fecha_nacimineto: string | null
}

const defaultValues = {
  id: '',
  nombre: '',
  apellido: '',
  nro_documento: '',
  email: '',
  nro_telefono: '',
  razon_social: '',
  ruc: '',
  ciudades: '',
  imagen_perfil: '',
  fecha_nacimineto: '',
}

export const PerfilTransportista: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore, transportistaStore } = rootStore
  const [Alert, setAlert] = useState(false)
  const [AlertContrato, setAlertContrato] = useState(false)
  const [FirebaseError, setFirebaseError] = useState<string | null>(null)
  const [FirebaseAutenticado, setFirebaseAutenticado] = useState(true) // Usuario ya autenticado
  const [HabilitarFirebase, setHabilitarFirebase] = useState(false)
  const [SpinnerCaptcha, setSpinnerCaptcha] = useState(false)
  const [NroTelefonoAnterior, setNroTelefonoAnterior] = useState<string | null>()
  const [ModalFoto, setModalFoto] = useState(false)
  const [ModalCodigoFirebaseVisible, setModalCodigoFirebaseVisible] = useState(false)
  const [CiudadesSeleccionadas, setCiudadesSeleccionadas] = useState([] as any)
  const FirebaseVerificationIdRef = useRef('')
  const addToast = useToaster()
  const openPage = useOpenPage()
  const toaster = useToaster()
  const apiToaster = useApiToaster()
  const [Editar, setEditar] = useState(false)
  const [Loading, setLoading] = useState(false)
  const [Datos, setDatos] = useState<DatosType>()
  const [Errors, setErrors] = useState<ErrorsType>()
  const [ImgPreview, setImgPreview] = useState('')

  const Content = useRef<HTMLIonContentElement | null>(null)

  const [Transportista, setTranspotista] = useState(transportistaStore.transportista)

  useEffect(() => {
    if (transportistaStore.transportista) {
      setTranspotista(transportistaStore.transportista)
      if (transportistaStore.transportista.usuario.imagen_perfil) {
        setImgPreview(transportistaStore.transportista.usuario.imagen_perfil)
      }
    }
  }, [transportistaStore.transportista])

  const setup = () => {
    if (Capacitor.getPlatform() !== 'web') {
      FirebaseAuthentication.signOut().catch((reason) =>
        console.log('error en FirebaseAuthentication.signOut:' + reason),
      )
    }
    if (Transportista) {
      const datos: any = { ...Datos }

      datos.id = Transportista.id
      datos.nombre = Transportista.usuario.first_name
      datos.apellido = Transportista.usuario.last_name
      datos.nro_documento = Transportista.usuario.nro_documento
      datos.nro_telefono = format_numero_telefono_input(Transportista.usuario.nro_telefono)
      setNroTelefonoAnterior(format_numero_telefono_input(Transportista.usuario.nro_telefono))
      datos.razon_social = Transportista.usuario.razon_social
      datos.ruc = Transportista.usuario.ruc
      datos.imagen_perfil = Transportista.usuario.imagen_perfil
      datos.email = Transportista.usuario.email
      setCiudadesSeleccionadas(Transportista ? Transportista.ciudades.map((ciu) => ciu.id) : [])
      setDatos(datos)
    }
  }

  useEffect(() => {
    console.log(Datos)
  }, [Datos])

  useEffect(() => {
    if (Capacitor.getPlatform() !== 'web') {
      FirebaseAuthentication.onAuthStateChanged().subscribe((user) => {
        // En caso de logout viene false
        // Ej. user: {"uid":"Mn9CjyoSeCcO1srXeiB7pZCmc2c2","phoneNumber":"+595981483296","providerId":"firebase","emailVerified":false}
        console.log('FirebaseAuthentication.onAuthStateChanged', !!user, user)
        if (FirebaseVerificationIdRef.current) {
          // Ignoramos en caso de que no sea de una verificacin en curso
          if (user) {
            setFirebaseAutenticado(true)
            setHabilitarFirebase(false)
            setFirebaseError(null)
            if (FirebaseVerificationIdRef.current !== '') {
              toaster('Nmero verificado exitosamente')
            }
            setModalCodigoFirebaseVisible(false)
          } else {
            setFirebaseAutenticado(false)
            setHabilitarFirebase(true)
          }
          FirebaseVerificationIdRef.current = ''
        }
      })
    }
  }, [])

  useEffect(() => {
    setup()
  }, [Transportista])

  useEffect(() => {
    if (!!Content && !!Content.current?.scrollToTop) {
      // @ts-ignore
      Content.current.scrollToTop(100)
    }
  }, [Editar])

  const imgSelected = async (data: any, tipo: any) => {
    if (Capacitor.getPlatform() !== 'web') {
      const contents = await Filesystem.readFile({
        path: data,
      })
      data = `data:image/jpeg;base64,${contents.data}`
    }
    setImgPreview(data)
    let blob = await fetch(data).then((r) => r.blob())
    const imagen_comprimida = await comprimirImagen(blob, tipo)

    const datos: any = { ...Datos }
    //@ts-ignore
    datos[tipo] = imagen_comprimida
    setDatos(datos)
  }

  const obtenerTransportista = (idTransportista: number) => {
    rootStore.environment.api.adminGetTransportista(idTransportista).then((result) => {
      if (result.kind === 'ok') {
        transportistaStore.setTransportista(result.transportista)
        // setDatos(result.transportista)
      }
    })
  }

  const setDato = (campo: string, valor: any) => {
    if (!!Datos) {
      const datos = { ...Datos }
      //@ts-ignore
      datos[campo] = valor
      setDatos(datos)
    }
  }

  const setDatoInput = (campo: string, data: any) => {
    setDato(campo, data)
  }

  const dataSelectorModalCiudades = (data: any) => {
    setCiudadesSeleccionadas(data)
  }

  const verificarNumeroMovil = async () => {
    if (Datos?.nro_telefono?.length !== 12) {
      toaster('Ingrese un nmero vlido', 'warning')
      return
    }
    setSpinnerCaptcha(true)
    setHabilitarFirebase(false)
    setFirebaseError(null)
    let telefono = format_numero_telefono_verificar(Datos?.nro_telefono)
    const verifyTimeout = !rootStore.isDebug ? 30000 : 1000
    FirebaseAuthentication.verifyPhoneNumber('+' + telefono, verifyTimeout)
      .then(async (verificationId: any) => {
        // En android este callback se llama sin esperar el verifyTimeout
        setHabilitarFirebase(true)
        setSpinnerCaptcha(false)
        if (FirebaseAutenticado) {
          setFirebaseError(null)
          return
        }
        FirebaseVerificationIdRef.current = verificationId
        setModalCodigoFirebaseVisible(true)
      })
      .catch((error: any) => {
        console.log('verifyPhoneNumber error', error)
        setFirebaseError(error)
        setFirebaseAutenticado(false)
        setHabilitarFirebase(true)
        setSpinnerCaptcha(false)
        toaster('Ocurri un error al solicitar la verificacin', 'error')
      })
  }

  const verificarNumeroWeb = () => {
    if (Datos?.nro_telefono?.length !== 12) {
      toaster('Ingrese un nmero vlido', 'warning')
      return
    }
    setSpinnerCaptcha(true)
    setFirebaseError(null)
    let telefono = format_numero_telefono_verificar(Datos?.nro_telefono)
    const applicationVerifier = new firebase.auth.RecaptchaVerifier('recaptcha-container')
    const provider = new firebase.auth.PhoneAuthProvider()
    provider
      .verifyPhoneNumber('+' + telefono, applicationVerifier)
      .then((verificationId) => {
        setModalCodigoFirebaseVisible(true)
        setSpinnerCaptcha(false)
        FirebaseVerificationIdRef.current = verificationId
        applicationVerifier.clear()
      })
      .catch((err) => {
        console.log('verifyPhoneNumber error', err)
        setSpinnerCaptcha(false)
        setFirebaseError(err.message)
        setFirebaseAutenticado(false)
        toaster('Ocurri un error al solicitar la verificacin', 'error')
        applicationVerifier.clear()
      })
  }

  const modalCodigoFirebaseCallback = (pin: any) => {
    setModalCodigoFirebaseVisible(false)
    if (!pin) {
      toaster('Se cancel la verificacin de su nmero', 'error')
      return
    }
    setSpinnerCaptcha(true)
    if (Capacitor.getPlatform() === 'web') {
      const phoneCredential = firebase.auth.PhoneAuthProvider.credential(FirebaseVerificationIdRef.current, pin)
      if (phoneCredential !== undefined) {
        firebase
          .auth()
          .signInWithCredential(phoneCredential)
          .then((r) => {
            setSpinnerCaptcha(false)
            setFirebaseAutenticado(true)
            setFirebaseError(null)
            toaster('Nmero verificado exitosamente')
          })
          .catch((err) => {
            if (
              err.message
                .toLowerCase()
                .indexOf('the sms verification code used to create the phone auth credential is invalid') !== -1
            ) {
              toaster('El cdigo de verificacin ingresado es incorrecto', 'warning')
              setModalCodigoFirebaseVisible(true)
            } else {
              setFirebaseError(err.message)
            }
            setSpinnerCaptcha(false)
            setFirebaseAutenticado(false)
          })
      } else {
        setSpinnerCaptcha(false)
        setFirebaseAutenticado(false)
        toaster('Ocurri un error al verificar su nmero de telfono, vuelva a intentarlo', 'error')
      }
    } else {
      FirebaseAuthentication.signInWithVerificationId(FirebaseVerificationIdRef.current, pin)
        .then((result: any) => {
          // En iOS result vuelve null, en Android result vuelve "OK"
          // Se asume que si lleg a este punto fue satisfactoria la verificacin
          setSpinnerCaptcha(false)
          setFirebaseAutenticado(true)
          setFirebaseError(null)
          toaster('Nmero verificado exitosamente')
        })
        .catch((error) => {
          console.log('error', error)
          if (
            error
              .toLowerCase()
              .indexOf('the sms verification code used to create the phone auth credential is invalid') !== -1
          ) {
            toaster('El cdigo de verificacin ingresado es incorrecto', 'warning')
            setModalCodigoFirebaseVisible(true)
          } else {
            setFirebaseError(error)
          }
          setFirebaseAutenticado(false)
          setSpinnerCaptcha(false)
        })
    }
  }

  const guardar = () => {
    setLoading(true)
    setErrors(defaultValues)
    let datos = { ...Datos }

    // @ts-ignore
    datos.ciudades = CiudadesSeleccionadas

    if (!!datos.nro_telefono) {
      datos.nro_telefono = format_numero_telefono_verificar(datos.nro_telefono)
    }

    rootStore.environment.api.guardarTransportista(datos).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setEditar(false)
        addToast('Perfil actualizado exitosamente', 'success')
        transportistaStore.setTransportista(result.transportista)
        history.push(RUTAS_APP.PERFIL, { guardado: true })
      } else {
        if (result.kind === 'bad-data') {
          setErrors(result.errors)
          if (result.errors.non_field_errors) {
            addToast(result.errors.non_field_errors[0], 'warning')
          }
        } else {
          addToast('Ocurri un error al guardar vuelva a intentar en unos momentos', 'error')
        }
      }
    })
  }

  return (
    <PageApp
      preset={'p-driver'}
      subBarraExternaHeader={<SubHeader links={[{ titulo: 'Perfil' }]} />}
      sub_titulo={` `}
      mostrar_barra={false}
    >
      <ModalVerificarPinFirebase
        visible={ModalCodigoFirebaseVisible}
        callback={modalCodigoFirebaseCallback}
        color={'var(--naranja-transportista'}
        class_boton_aceptar="btn-principal-transportista"
        close={() => {
          setModalCodigoFirebaseVisible(false)
        }}
      />

      {!!Transportista && (
        <>
          <>
            <IonContent scrollEvents={true} ref={(ref) => (Content.current = ref)}>
              <div className={'d-flex flex-column pt-3'} style={{ justifyContent: 'space-between', height: '100%' }}>
                <div className={'row'}>
                  <div className={'col-12'}>
                    <div className={'d-flex flex-row'}>
                      <div className={'imagen-perfil-container d-flex mb-3'}>
                        {Transportista.usuario.imagen_perfil ? (
                          <div>
                            <PhotoLoaderModalRegistroMultiple
                              perfil={true}
                              color="var(--naranja-transportista)"
                              titulo_modal="Subir Foto de perfil"
                              titulo_label={''}
                              bloqueado={!Editar}
                              custom_boton={<img alt="Imagen de perfil" className={'imagen-perfil'} src={ImgPreview} />}
                              imageItems={[
                                {
                                  titulo: 'Imagen de perfil',
                                  userDataLabel: 'imagen_perfil',
                                  preview: Transportista.usuario.imagen_perfil
                                    ? Transportista.usuario.imagen_perfil
                                    : 'assets/icon/driver.png',
                                },
                              ]}
                              on_select={imgSelected}
                            />
                            {/* */}
                          </div>
                        ) : (
                          <span>
                            IMAGEN <br /> PERFIL
                          </span>
                        )}
                      </div>
                      <div className={'flex-grow-1'}>
                        <div className={'text-gray'}>
                          <strong>
                            {Transportista.usuario.first_name} {Transportista.usuario.last_name}
                          </strong>
                        </div>
                        <div className={'text-gray'}>
                          ID#{transportistaStore.transportista ? transportistaStore.transportista.id : ''}
                        </div>
                        <div className={'text-gray rating'}>
                          {transportistaStore.transportista ? transportistaStore.transportista.rating : ''}
                          <StarIcon style={{ marginRight: 5, fontSize: 15, color: '#FFA300' }} />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className={'mx-1'} id="datos-transportista-perfil">
                  <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr' }}>
                    <div>
                      <strong className="campo-perfil-datos"> RUC</strong>
                      <br />
                      <AdminItemFieldRegistro
                        deshabilitado={Editar && !Loading}
                        titulo={'RUC'}
                        campo={'ruc'}
                        tipo={'texto'}
                        set_dato={setDato}
                        valor={Datos?.ruc}
                        columnas={12}
                        error={Errors?.ruc}
                      />
                    </div>
                    <div>
                      <strong className="campo-perfil-datos"> Razn Social</strong>
                      <br />
                      <AdminItemFieldRegistro
                        deshabilitado={Editar && !Loading}
                        titulo={'Razn social'}
                        tipo={'texto'}
                        campo={'razon_social'}
                        set_dato={setDato}
                        valor={Datos?.razon_social}
                        columnas={12}
                        error={Errors?.razon_social}
                      />
                    </div>
                  </div>
                  <div>
                    <strong className="campo-perfil-datos"> Correo</strong>
                    <br />
                    <AdminItemFieldRegistro
                      deshabilitado={Editar && !Loading}
                      titulo={'Correo'}
                      tipo={'email'}
                      campo={'email'}
                      set_dato={setDato}
                      valor={Datos?.email}
                      error={Errors?.email}
                      columnas={12}
                    />
                  </div>

                  <div>
                    <strong className="campo-perfil-datos"> Celular</strong>
                    <br />
                    {!Editar && <AdminItemDetail texto={Datos?.nro_telefono} columnas={12} />}
                    {Editar && (
                      <AdminItemFieldRegistro
                        deshabilitado={Editar && !Loading && !FirebaseAutenticado}
                        titulo={'Celular'}
                        tipo={'telefono'}
                        campo={'nro_telefono'}
                        set_dato={setDatoInput}
                        valor={Datos?.nro_telefono}
                        error={Errors?.nro_telefono}
                        verificar_firebase_habilitado={HabilitarFirebase}
                        verificar_firebase_autenticado={FirebaseAutenticado}
                        verificar_firebase_func={
                          Capacitor.getPlatform() === 'web' ? verificarNumeroWeb : verificarNumeroMovil
                        }
                        verificar_firebase_error={FirebaseError ? FirebaseError : ''}
                        verificar_firebase_reset={() => {
                          setSpinnerCaptcha(false)
                          setFirebaseError(null)
                          setFirebaseAutenticado(false)
                          setHabilitarFirebase(true)
                          if (Capacitor.getPlatform() !== 'web') {
                            FirebaseAuthentication.signOut()
                          }
                        }}
                        columnas={12}
                      />
                    )}
                  </div>
                  {!Editar && (
                    <div>
                      <strong className="campo-perfil-datos"> Fecha de nacimiento</strong>
                      <br />
                      <AdminItemDetail
                        texto={loginStore.fecha_nacimiento ? moment(loginStore.fecha_nacimiento).format('LL') : '-'}
                        columnas={12}
                      />
                    </div>
                  )}

                  <div>
                    {!Editar && (
                      <>
                        <strong className="campo-perfil-datos"> Ciudades de cobertura</strong>
                        <br />
                        <AdminItemFieldRegistro
                          deshabilitado={true}
                          titulo={'Ciudades de cobertura'}
                          tipo={'textarea'}
                          campo={'ciudades'}
                          set_dato={setDato}
                          valor={
                            !!Transportista.ciudades
                              ? Transportista.ciudades.map((ciu: any) => ciu.nombre).join(', ')
                              : ''
                          }
                          columnas={12}
                        />
                      </>
                    )}
                    {Editar && (
                      <>
                        <SelectorModalRegistro
                          titulo={'Ciudades de cobertura'}
                          titulo_label={'Ciudades de cobertura'}
                          listado_elementos={rootStore.ciudades}
                          listado_elementos_seleccionados={!!Transportista ? Transportista.ciudades : []}
                          datos_filtrados={dataSelectorModalCiudades}
                          multiple={true}
                          llave_label_opcion={'nombre'}
                        />
                        <span className="text-danger"> {Errors?.ciudades}</span>
                      </>
                    )}
                  </div>
                </div>

                <FooterFormulario className="py-4">
                  {Editar && (
                    <div
                      style={{
                        display: 'grid',
                        gridTemplateColumns: ' 1fr',
                        width: '100%',
                        maxWidth: 1200,
                        gap: 15,
                        paddingLeft: 20,
                        paddingRight: 20,
                      }}
                    >
                      <button className={'btn-principal-transportista'} onClick={() => guardar()}>
                        {Loading && <IonSpinner name="crescent" />}
                        {!Loading && <span>Guardar</span>}
                      </button>
                    </div>
                  )}
                  {!Editar && (
                    <div
                      style={{
                        display: 'grid',
                        gridTemplateColumns: '1fr 1fr',
                        width: '100%',
                        maxWidth: 1200,
                        gap: 15,
                        paddingLeft: 20,
                        paddingRight: 20,
                      }}
                    >
                      <button className={'btn-principal-transportista'} onClick={() => setEditar(!Editar)}>
                        {Loading && <IonSpinner name="crescent" />}
                        {!Loading && <span>Editar perfil</span>}
                      </button>

                      <button
                        className={'btn-principal-transportista'}
                        onClick={() => history.push(RUTAS_APP.CONFIGURACION)}
                      >
                        <span>Configuracin</span>
                      </button>
                    </div>
                  )}
                </FooterFormulario>
              </div>
            </IonContent>
          </>
        </>
      )}
    </PageApp>
  )
})

export default PerfilTransportista
